.allCardsGame {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2% 1%;
    height: 100vh;
    margin: 5% auto;
}

.cardsGame {
    width: 100%;
    margin: 5px;
    position: relative;
    border-radius: 1rem;
    box-shadow: 0px 0px 3px rgb(193, 192, 192);
    transform: scale(1);
    transform-style: preserve-3d;
    transition: transform .5s;
}

.cardsGame:active {
    transform: scale(0.97);
    transition: transform .2s;
}

.cardsGame.flip {
    transform: rotateY(180deg);
}

.cardGameFront,
.cardGameBack {
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 1rem;
    background-image: url('../../public/pict/BALL-FOOTBALL.jpeg');
    background-position: center;
    background-size: cover;
    backface-visibility: hidden;
}

.cardGameFront {
    transform: rotateY(180deg);
    background: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cardGameFront img {
    width: 70%;
    height: 100%;
    border-radius: 1rem;
}

.winGame {
    display: none;
}

.displayNoneCardsGame {
    display: none;
}

.displayWinBlock {
    display: block;
}